import React, { Component } from 'react'



export default class EncuestaItem extends Component {

    render() {
        return (
            <tr>
                                        <th scope="row">{this.props.id}</th>
                                        <td>{this.props.nombre}</td>
                                        <td>{this.props.apellidos}</td>
                                        <td>{this.props.edad}</td>
                                        <td>{this.props.lenguaje}</td>
                                        </tr>
        )
    }
}