import React, { useState, useEffect } from 'react';
import Login from './components/Login'
import EncuestaRegistro from './components/EncuestaRegistro'
import EncuestaListado from './components/EncuestaListado'

//const urlApiEncuestaBase = 'http://localhost:8080';
const urlApiEncuestaBase = 'http://balanceador-cf-trabajo-final-1394212781.us-east-1.elb.amazonaws.com';

function getToken (){
  let store = JSON.parse(localStorage.getItem('login'));
  if(store && store.login){
    return store.token;
  }
  return '';
}

function logout(){
  localStorage.removeItem('login');
  window.location.href = '';
}

function parseJwt (token) {
  var base64Url = token.split('.')[1];
  var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
  var jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
      return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
  }).join(''));

  return JSON.parse(jsonPayload);
};

function App() {
  

  const [login, setLogin] = useState(false);
  const [rol, setRol] = useState(null);

  useEffect(() => {
    if(getToken()!=''){
        const datosJwt = parseJwt(getToken());
        setLogin(true)
        setRol(datosJwt["cognito:groups"][0]);
      return;
    }else{
      setLogin(false)
    }
  }, []);

  
  return (
    <div className="app container">
      <div className="jumbotron">
  <h1 className="text-center mb-3 text-info">Sistema de Encuesta {rol}</h1>
        { 
          !login? <Login urlAPi={urlApiEncuestaBase}/> : 
          <div>
            {
              rol=='ADMIN'? <EncuestaListado urlAPi={urlApiEncuestaBase} />: <EncuestaRegistro urlAPi={urlApiEncuestaBase} />
            }
            
          </div>
          
        }
        
      </div>

    </div>
  );
}

export default App;
