import React, { Component } from 'react'
import EncuestaItem from './EncuestaItem'

export default class EncuestaListado extends Component {

    constructor(props) {
        super(props)
        this.state = { encuestas: [] }
    }

    validarToken = () => {
        let store = JSON.parse(localStorage.getItem('login'));

        if(store && store.login){
            return store.token;
        }
        return '';
    }

    componentWillMount() {        
        const urlListado= this.props.urlAPi+`/api/v1/encuestas`;
        const token = this.validarToken();

        fetch(urlListado,{
            headers: new Headers({
            'Authorization': 'Bearer '+token
          })})
          .then((response) => response.json())
          .then((response) => {
            this.setState({ encuestas: response })
        })
    }
    
    logout =() => {
        localStorage.removeItem('login');
        window.location.href = '';
    }

    render() {
        return (
            <div>
                <p className="text-right" onClick={()=>{ this.logout()}}>Cerrar Sesi칩n <i className="fas fa-arrow"></i></p> 

                <div className="row justify-content-md-center">
                    <div className="col-12">

                        <div className="card">
                            <div className="card-body">
                                <h5 className="card-title ">Resultados</h5>
                                <table className="table table-striped">
                                    <thead>
                                        <tr>
                                        <th scope="col">#</th>
                                        <th scope="col">Nombres</th>
                                        <th scope="col">Apellidos</th>
                                        <th scope="col">Edad</th>
                                        <th scope="col">Elecci칩n</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                    {
                                        this.state.encuestas.map((item, i) => {
                                            return <EncuestaItem key={item.id} 
                                                        id={item.id} 
                                                        nombre={item.name} 
                                                        apellidos={item.lastName} 
                                                        edad={item.age}
                                                        lenguaje={item.language}
                                                    />
                                        })
                                    }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
