import React, { Component, useState, useEffect } from 'react'
import EncuestaRegistro from './EncuestaRegistro';

export default class Login extends Component {

    constructor(){
        super();
        this.state = {
            login:false
        }
    }

    handleSubmit = (e) => {
        e.preventDefault();
        const username = this.username.value;
        const password =  this.password.value;
        
        if(username == '' || password ==''){
            return alert('Complete todos los campos.');
        }
        const data = {
            username,
            password
        }

        this.login(data);
    }

    login = (data) =>{
        const urlLogin= this.props.urlAPi+`/api/v1/security/login`;
        try {
            
            fetch(urlLogin,{
                method: 'POST',
                body: JSON.stringify(data),
                headers: new Headers({
                'Accept': 'application/json',
                'Content-Type': 'application/json'
                })})
                .then((response) => response.json())
                .then((response) => {
                    console.log(response);
                    if(response.status == 'OK'){
                        localStorage.setItem('login', JSON.stringify({
                            login: true,
                            token: response.accessToken
                        }));
        
                        this.setState({login:true});
                        
                        window.location.href = '';
                    }else{
                        alert(response.body);
                    }
            });
        } catch (error) {
            alert('Error: Intente nuevamente');
        }
    }

    render() {
        return (
            <div>
                {
                    !this.state.login?
                        <div className="row justify-content-md-center">
                        <div className="col-md-8 col-xl-6">
                            <div className="card">
                                <div className="card-body">
                                    <h5 className="card-title ">Ingresar al Sistema</h5>
                                    
                                    <form onSubmit={this.handleSubmit} autoComplete="off">
                                        <div className="form-group">
                                            <input type="text" className="form-control"placeholder="Usuario"  ref={(input)=>this.username = input}/>
                                        </div>

                                        <div className="form-group">
                                            <input type="password" className="form-control" placeholder="Contrase침a" ref={(input)=>this.password = input}/>
                                        </div>
                                        <button type="submit" className="btn btn-lg btn-danger btn-block">Ingresar</button>
                                    </form>

                                </div>
                            </div>
                        </div>
                    </div>
                    : <EncuestaRegistro />

                }
                <br /><br />
            </div>
        )
    }
}
