import React, { Component } from 'react';

class EncuestaRegistro extends Component {

    handleSubmit = (e) => {
        e.preventDefault();
        const name = this.name.value;
        const lastName =  this.lastName.value;
        const age = this.age.value;
        const language = this.language.value;
        
        if(name == '' || lastName =='' || age == '' || lastName =='' || language ==''){
            return alert('Complete todos los campos.');
        }

        const data = {
            name,
            lastName,
            age: parseInt(age),
            language
        }

        const token = this.validarToken();
        this.registrarEncuesta(data, token);
    }

    validarToken = () => {
        let store = JSON.parse(localStorage.getItem('login'));

        if(store && store.login){
            return store.token;
        }
        return '';
    }

    registrarEncuesta = (data, token ) => {
        const urlRegistro= this.props.urlAPi+`/api/v1/encuestas`;

        console.log(data);
        fetch(urlRegistro,{
            method: 'POST',
            body: JSON.stringify(data),
            headers: new Headers({
            'Authorization': 'Bearer '+token,
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          })})
          .then((response) => response.json())
          .then((response) => {
              console.log(response);
              alert('Registro exitoso, gracias por participar.');
        });

    }

    logout =() => {
        localStorage.removeItem('login');
        window.location.href = '';
    }

    render() { 
        return (
            <div>
                <p className="text-right" onClick={()=>{ this.logout()}}>Cerrar Sesi칩n <i className="fas fa-arrow"></i></p> 
                <div className="row justify-content-md-center">
                    <div className="col-md-8 col-xl-6">
                        <div className="card">
                            <div className="card-body">
                                <h5 className="card-title ">Registrar Voto</h5>
                                
                                    <form onSubmit={this.handleSubmit} autoComplete="off">
                                        <div className="form-group">
                                            <label htmlFor="name">Nombres</label>
                                            <input type="text" className="form-control" id="name" placeholder="Nombres" ref={(input)=>this.name = input}/>
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="lastName">Apellidos</label>
                                            <input type="text" className="form-control" id="lastName" placeholder="Apellidos" ref={(input)=>this.lastName = input}/>
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="age">Edad</label>
                                            <input type="number" className="form-control" id="age" placeholder="Edad" maxLength="2" ref={(input)=>this.age = input}/>
                                        </div>

                                        <div className="form-check mb-2 text-center">
                                            <input className="form-check-input" type="radio" id="radioJava" name="language" value="Java" onClick={(input) => { this.language.value= 'Java' }}/>
                                            <label className="form-check-label" htmlFor="radioJava">
                                                Java
                                            </label>
                                        </div>

                                        <div className="form-check mb-4 text-center">
                                            <input className="form-check-input" type="radio" id="radioC" name="language" value="C#" onClick={(input) => { this.language.value= 'C#' }}/>
                                            <label className="form-check-label" htmlFor="radioC">
                                                C#&nbsp;&nbsp;&nbsp;
                                            </label>
                                            <input type="hidden" ref={(input)=>this.language = input}/>
                                        </div>

                                        <button type="submit" className="btn btn-lg btn-danger btn-block">Votar</button>
                                    </form>

                            </div>
                        </div>
                    </div>
                </div>

                <br /><br />
            </div>
        );
    }
}
 
export default EncuestaRegistro;